import React from 'react';
import "./CostCalc.css";

const CostCalc=(props)=>{
    const fuelPrice=6.61
    var totalCost=0
    
    let avaliableModelsNumValue=1
    if(props.avaliableModelsNum<3)avaliableModelsNumValue=1.15

    let driverLicensePass=1
    if(props.currentYear-props.driverLicenseYear<5)driverLicensePass=1.2

    if((props.currentYear-props.driverLicenseYear<3)&&(props.pickedPriceCategory===2)){
        alert("Nie moesz wypoyczy tego pojazdu!");
        return(<div></div>);
    }
    return(
        <div id="costCalc">
            <table width="100%">
                <tr>
                    <td>Dzienna cena pojazdu z iloci dni</td>
                    <td>{props.basePrice}z*{props.daysNumber}</td>
                </tr>
                <tr>
                    <td>Kategoria cenowa wybranego samochodu</td>
                    <td>{totalCost=Number(Math.round(props.basePrice*props.daysNumber+'e+2')+'e-2')}z*{props.pickedPriceCategory}</td>
                </tr>
                <tr>
                    <td>Procent od dugoci posiadania prawa jazdy</td>
                    <td>{totalCost=Number(Math.round(totalCost*props.pickedPriceCategory+'e+2')+'e-2')}z*{driverLicensePass}</td>
                </tr>
                <tr>
                    <td>Procent od iloci dostpnych egzemplarzy</td>
                    <td>{totalCost=Number(Math.round(totalCost*driverLicensePass+'e+2')+'e-2')}z*{avaliableModelsNumValue}</td>
                </tr>
                <tr>
                    <td>Koszt paliwa</td>
                    <td>{totalCost=Number(Math.round(totalCost*avaliableModelsNumValue+'e+2')+'e-2')}z+{Number(Math.round(props.kmAmount*props.avgCombustion/100*fuelPrice+'e+2')+'e-2')}z</td>
                </tr><br/>
                <tr className="totalCosts">
                    <td>Kwota wynajmu netto:</td>
                    <td>{totalCost=Number(Math.round(totalCost+props.kmAmount*props.avgCombustion/100*fuelPrice+'e+2')+'e-2')}z</td>
                </tr>
                <tr className="totalCosts">
                    <td>Kwota wynajmu brutto:</td>
                    <td>{totalCost=Number(Math.round(totalCost*1.23+'e+2')+'e-2')}z</td>
                </tr>
            </table>
        </div>
    );
}

export default CostCalc;